.card {
    width: 30%;
    box-shadow: 0 0 8px gray;
    padding: 1rem;
    text-align: center;
    border-radius: 15px;
    transition-duration: 0.6s;
    cursor: pointer;
    margin-top: 10px;

}

.card h3 {
    margin-top: 15px ;
    margin-bottom: 20px;
}

.card p{
    color: red;
}

.card:hover {
    transform: translateY(-8px);
    background-color: #d8d8d8cc;
    box-shadow: 0 0 8px red;
    border-radius: 10px;
}

.card .img {
    width: 100%;
    height: 360px;
}

.card .img img {
    width: 100%;
    height: 100%;
}